body {
  font-family: 'Helvetica Neue', Arial, sans-serif;
  background-color: #f4f4f9;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 900px;
  margin: 40px auto;
  background-color: white;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  overflow: hidden;
}

.sidebar {
  background-color: #2C3E50;
  color: white;
  padding: 20px;
  width: 250px;
  flex-shrink: 0;
}

.sidebar h1 {
  font-size: 1.8em;
  margin-bottom: 10px;
}

.sidebar p {
  margin: 10px 0;
  color: #ecf0f1;
}

.sidebar a {
  color: #ecdbff;
  text-decoration: none;
}

.sidebar a:hover {
  text-decoration: underline;
}

.content {
  padding: 20px;
  flex-grow: 1;
}

h2 {
  color: #2C3E50;
  border-bottom: 2px solid #ecf0f1;
  padding-bottom: 5px;
  margin-bottom: 10px;
  font-size: 1.4em;
}

p {
  margin: 10px 0;
  line-height: 1.6;
}

.skills-list,
.languages-list {
  display: flex;
  flex-wrap: wrap;
}

.skills-list ul,
.languages-list ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  width: 50%;
}

ul li {
  margin-bottom: 5px;
}

a {
  color: #3498db;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}
